import React, { useState } from 'react';
import {Box, TextField, Button, Typography, Container, FormControl, InputLabel, FormHelperText} from '@mui/material';
import { Link } from 'react-router-dom';

// Import axios
import axios from '../../api/axiosConfig';

// import Alert
import {SwalAlert, SwalAlertCallBack} from "../../components/Common/SwalAlert";

function SignUp() {
    const [id, setId] = useState('');
    const [check, setCheck] = useState(false);
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [occupation, setOccupation] = useState('');

    const changeId = async (id: string) => {
        setId(id);

        if (id.length >= 6) {
            try {
                const response = await axios.get(`/api/auth/checkid/${id}`);
                if (response.data.success) {
                    setCheck(true);
                    //    
                } else {
                    setCheck(false);
                    SwalAlert('error', ' ', '   .');
                }
            } catch (error) {
                //   
                SwalAlert('error', ' ', '  .    .');
            }
        } else {
            setCheck(false);
        }
    }



    const handleSignUp = () => {

        // Validation
        if (id === '' || name === '' || email === '' || phone === '' || password === '' || confirmPassword === '' || occupation === '') {
            SwalAlert('warning', ' ', '  .');
        } else if (id.length < 6) {
            SwalAlert('warning', ' ', ' 6  .');
        } else if (password.length < 8) {
            SwalAlert('warning', ' ', ' 8  .');
        } else if (password !== confirmPassword) {
            SwalAlert('warning', ' ', '  .');
        } else {

            axios.post('/api/auth/signup', {
                id: id,
                name: name,
                email: email,
                phone: phone,
                password: password,
                occupation: occupation,
            }).then((res) => {
                if (res.data.success) {
                    //         
                    SwalAlertCallBack('success', ' ', '  .', () => {
                        window.location.href = '/login'; //   
                    });
                } else {
                    SwalAlert('error', ' ', ' .');
                }
            }).catch((err) => {
                SwalAlert('error', ' ', ' .');
            })
        }

    }

    return (
        <Container maxWidth="sm">
            <Box
                sx={{
                    marginTop: 8,
                    marginBottom: 8,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <Typography component="h1" variant="h6" fontWeight={'bold'}>
                    SIGN UP
                </Typography>
                <Box
                     sx={{
                         mt: 7,
                         p: 7,
                         backgroundColor: 'rgb(247, 247, 247)',
                         border: '1px solid rgb(249, 249, 249)',
                         borderRadius: '10px',
                     }}>

                    <FormControl fullWidth error={!check && id.length >= 6}>
                        <InputLabel
                            htmlFor="id"
                            sx={{
                                color: 'black',
                                fontSize: '14px',
                                fontWeight: 'bold',
                                position: 'relative',
                                top: '-5px',
                                left: '-10px',
                            }}
                        >
                            ID
                        </InputLabel>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="id"
                            name="id"
                            autoComplete="id"
                            autoFocus
                            value={id}
                            onChange={(e) => changeId(e.target.value)}
                            placeholder=" ."
                            InputProps={{
                                sx: {
                                    '&.Mui-focused fieldset': {
                                        outline: 'none',
                                        borderColor: 'transparent',
                                    },
                                    backgroundColor: 'white',
                                    border: id === "" ? '1px solid rgb(249, 249, 249)' : '1px solid rgb(41, 41, 46)',
                                    minWidth: '250px',
                                    minHeight: '40px',
                                    fontSize: '13px',
                                },
                            }}
                        />
                        {
                            id.length >= 6 && (
                                <FormHelperText>
                                    {check ? '  .' : '   .'}
                                </FormHelperText>
                            )
                        }
                    </FormControl>


                    <FormControl fullWidth>
                        <InputLabel
                            htmlFor="name"
                            sx={{
                                color: 'black',
                                fontSize: '14px',
                                fontWeight: 'bold',

                                // Input   
                                position: 'relative',
                                top: '-5px',
                                left: '-10px',
                            }}>
                            NAME
                        </InputLabel>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="name"
                            name="name"
                            autoComplete="name"
                            autoFocus
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            placeholder={" ."}
                            InputProps={{
                                sx: {
                                    //    
                                    '&.Mui-focused fieldset': {
                                        outline: 'none',
                                        borderColor: 'transparent',  //   
                                    },

                                    backgroundColor: 'white',
                                    border: name === "" ? '1px solid rgb(249, 249, 249)' : '1px solid rgb(41, 41, 46)',
                                    minWidth: '250px',
                                    minHeight: '40px',
                                    fontSize: '13px',
                                },
                            }}
                        />
                    </FormControl>

                    <FormControl fullWidth>
                        <InputLabel
                            htmlFor="email"
                            sx={{
                                color: 'black',
                                fontSize: '14px',
                                fontWeight: 'bold',

                                // Input   
                                position: 'relative',
                                top: '-5px',
                                left: '-10px',
                            }}>
                            EMAIL
                        </InputLabel>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            name="email"
                            autoComplete="email"
                            autoFocus
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            placeholder={" ."}
                            InputProps={{
                                sx: {
                                    //    
                                    '&.Mui-focused fieldset': {
                                        outline: 'none',
                                        borderColor: 'transparent',  //   
                                    },

                                    backgroundColor: 'white',
                                    border: email === "" ? '1px solid rgb(249, 249, 249)' : '1px solid rgb(41, 41, 46)',
                                    minWidth: '250px',
                                    minHeight: '40px',
                                    fontSize: '13px',
                                },
                            }}
                        />
                    </FormControl>

                    <FormControl fullWidth>
                        <InputLabel
                            htmlFor="phone"
                            sx={{
                                color: 'black',
                                fontSize: '14px',
                                fontWeight: 'bold',

                                // Input   
                                position: 'relative',
                                top: '-5px',
                                left: '-10px',
                            }}>
                            PHONE
                        </InputLabel>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="phone"
                            name="phone"
                            autoComplete="phone"
                            autoFocus
                            value={phone}
                            onChange={(e) => setPhone(e.target.value)}
                            placeholder={" (-) ."}
                            InputProps={{
                                sx: {
                                    //    
                                    '&.Mui-focused fieldset': {
                                        outline: 'none',
                                        borderColor: 'transparent',  //   
                                    },

                                    backgroundColor: 'white',
                                    border: phone === "" ? '1px solid rgb(249, 249, 249)' : '1px solid rgb(41, 41, 46)',
                                    minWidth: '250px',
                                    minHeight: '40px',
                                    fontSize: '13px',
                                },
                            }}
                        />
                    </FormControl>

                    <FormControl fullWidth>
                        <InputLabel
                            htmlFor="password"
                            sx={{
                                color: 'black',
                                fontSize: '14px',
                                fontWeight: 'bold',

                                // Input   
                                position: 'relative',
                                top: '-5px',
                                left: '-10px',
                            }}>
                            PASSWORD
                        </InputLabel>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="password"
                            name="password"
                            autoComplete="password"
                            type="password"
                            autoFocus
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            placeholder={" . (8 )"}
                            InputProps={{
                                sx: {
                                    backgroundColor: 'white',
                                    border: password === "" ? '1px solid rgb(249, 249, 249)' : '1px solid rgb(41, 41, 46)',
                                    minWidth: '250px',
                                    minHeight: '40px',
                                    fontSize: '13px',
                                },
                            }}
                        />
                    </FormControl>

                    <FormControl fullWidth>
                        <InputLabel
                            htmlFor="passwordCheck"
                            sx={{
                                color: 'black',
                                fontSize: '14px',
                                fontWeight: 'bold',

                                // Input   
                                position: 'relative',
                                top: '-5px',
                                left: '-10px',
                            }}>
                            PASSWORD CONFIRM
                        </InputLabel>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="passwordCheck"
                            name="passwordCheck"
                            autoComplete="passwordCheck"
                            type="password"
                            autoFocus
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                            placeholder={"  . (8 )"}
                            InputProps={{
                                sx: {
                                    backgroundColor: 'white',
                                    border: confirmPassword === "" ? '1px solid rgb(249, 249, 249)' : '1px solid rgb(41, 41, 46)',
                                    minWidth: '250px',
                                    minHeight: '40px',
                                    fontSize: '13px',
                                },
                            }}
                        />
                    </FormControl>


                    <FormControl fullWidth>
                        <InputLabel
                            htmlFor="occupation"
                            sx={{
                                color: 'black',
                                fontSize: '14px',
                                fontWeight: 'bold',

                                // Input   
                                position: 'relative',
                                top: '-5px',
                                left: '-10px',
                            }}>
                            OCCUPATION
                        </InputLabel>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="occupation"
                            name="occupation"
                            autoComplete="occupation"
                            autoFocus
                            value={occupation}
                            onChange={(e) => setOccupation(e.target.value)}
                            placeholder={" . (ex : , ,  ,  )"}
                            InputProps={{
                                sx: {
                                    //    
                                    '&.Mui-focused fieldset': {
                                        outline: 'none',
                                        borderColor: 'transparent',  //   
                                    },

                                    backgroundColor: 'white',
                                    border: phone === "" ? '1px solid rgb(249, 249, 249)' : '1px solid rgb(41, 41, 46)',
                                    minWidth: '250px',
                                    minHeight: '40px',
                                    fontSize: '13px',
                                },
                            }}
                        />
                    </FormControl>



                    <Box
                        sx={{
                            //  
                            display: 'flex',
                            justifyContent: 'flex-end',
                            alignItems: 'center',
                            mt: 1,
                            mb: 1,

                            gap: '7%',
                        }}
                    >
                        <Typography
                            variant="body2"
                            sx={{
                                color: 'rgba(0, 0, 0, 0.54)',
                                fontSize: '12px',
                                cursor: 'pointer',
                            }}
                            component={Link}
                            to="/login"
                        >
                             ?
                        </Typography>
                    </Box>

                    <Button
                        fullWidth
                        variant="contained"
                        sx={{
                            mt: 3,
                            backgroundColor: 'rgb(41, 41, 46)',
                            minHeight: '40px',
                            fontWeight: 'bold',

                            '&:hover': {
                                backgroundColor: 'rgba(41, 41, 46, .9)',
                            }

                            // '&:hover': {
                            //     backgroundColor: 'transparent',
                            //     border: '1px solid rgb(41, 41, 46)',
                            //     color: 'rgb(41, 41, 46)',
                            //     boxShadow: 'none',
                            // }
                        }}
                        onClick={handleSignUp}
                    >
                        SIGN UP
                    </Button>

                </Box>
            </Box>
        </Container>
    );
}

export default SignUp;
